import { Box, Checkbox, Group, NativeSelect, TextInput } from '@mantine/core'
import '@mantine/core/styles.css'
import { useForm } from '@mantine/form'
import { notifications } from '@mantine/notifications'
import React from 'react'
import { sendMessage } from '../api/telegram'

function FormBlock() {
	// const [isLoading, setIsLoading] = useState(false)

	const form = useForm({
		initialValues: {
			// email: '',
			city: '',
			phoneNumber: '',
			initial: '',
			message1: '',
			department: '',
			delivery: '  ',
			confirmation: true,
			
		},

		validate: {
			// email: value => (/^\S+@\S+$/.test(value) ? null : 'Invalid email'),
			phoneNumber: value =>
				/^\S/.test(value) ? null : '   ',
			initial: value => (/^\S/.test(value) ? null : '   '),
			department: value => (/^\S/.test(value) ? null : '   '),
			city: value => (/^\S/.test(value) ? null : '   '),
		},
	})

	const handleSubmit = async ({  phoneNumber, initial, message1, department, city, delivery, confirmation}) => {
		try {
			const message = `
			|| : ${initial} \n 
			||  : ${phoneNumber} \n
			|| : ${city} \n 
			||   : ${department} \n 
			||  : ${delivery}\n
			|| : ${
				message1 || ' '
			 } \n
			||   : ${confirmation ? "":""}`

			await sendMessage(message)
			// console.log(message)

			notifications.show({
				autoClose: 10000,
				title: '  ',
				message:
					'         ',
			})

			// setIsLoading(true)
		} catch (error) {
			form.setFieldError('email', error)
		}
		//  finally {
		// 	setIsLoading(false)
		// }
	}
	return (
		<Box miw={350} mx={10} className='form-box'>
			<form onSubmit={form.onSubmit(handleSubmit)}>
				<TextInput
					withAsterisk
					label=''
					placeholder=' '
					{...form.getInputProps('initial')}
				/>
				{/* <TextInput
					withAsterisk
					label='Email'
					placeholder='your@email.com'
					{...form.getInputProps('email')}
				/> */}
				<TextInput
					withAsterisk
					label=' '
					placeholder='+380'
					{...form.getInputProps('phoneNumber')}
				/>
				<TextInput
					withAsterisk
					label=''
					placeholder='  '
					{...form.getInputProps('city')}
				/>
				<TextInput
					withAsterisk
					label='  '
					placeholder='  '
					{...form.getInputProps('department')}
				/>
				<NativeSelect
      
      // onChange={(event) =>  setDelivery(event.currentTarget.value)}
      data={['  ','  ', ]}
			label='  '
			{...form.getInputProps('delivery', { type: 'nativeselect' })}
			
    />
				<TextInput
					label='/'
					placeholder='  ,  '
					{...form.getInputProps('message1')}
				/>
				<Checkbox
					defaultChecked
					
					// onChange={(event) => setChecked(event.currentTarget.checked)}
          mt="md"
					color="orange"
          label="    "
          {...form.getInputProps('confirmation', { type: 'checkbox' })}
        />

				<Group mt='md'>
					{/* <Button loading={isLoading} type='submit'>
						
					</Button>
					<Button loading={isLoading} type='submit' className='order-button'>
						
					</Button> */}
					<button id="bottom" className='order-button'></button>
				</Group>
			</form>
		</Box>
	)
}

export default FormBlock